<script>
import 'bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.bundle.min.js'
import { ref, onMounted, onUnmounted, computed } from 'vue'
import { Swiper, SwiperSlide } from 'swiper/vue'
import 'swiper/css'
const API_URL = 'http://localhost:3000/ScrollImages'
export default {
  name: 'HomeView',
  components: {
    Swiper,
    SwiperSlide
  },
  setup () {
    const images = ref([])
    const currentIndex = ref(0)
    const preview = ref({ left: null, right: null })
    const autoplayInterval = ref(null)
    const getImageUrl = (url) => {
      return require(`@/assets/images/${url.split('/').pop()}`)
    }
    const displayedImages = computed(() => {
      const len = images.value.length
      if (len === 0) return []
      return [
        images.value[currentIndex.value % len],
        images.value[(currentIndex.value + 1) % len],
        images.value[(currentIndex.value + 2) % len]
      ]
    })
    const startAutoplay = () => {
      if (autoplayInterval.value) {
        clearInterval(autoplayInterval.value)
      }
      autoplayInterval.value = setInterval(() => {
        nextSlide()
      }, 5000)
    }
    const stopAutoplay = () => {
      if (autoplayInterval.value) {
        clearInterval(autoplayInterval.value)
        autoplayInterval.value = null
      }
    }
    onMounted(async () => {
      try {
        const response = await fetch(API_URL)
        const data = await response.json()
        images.value = data.sort(() => Math.random() - 0.5).slice(0, 5)
        startAutoplay()
      } catch (error) {
        console.error(' JSON ', error)
      }
    })
    onUnmounted(() => {
      stopAutoplay()
    })
    const nextSlide = () => {
      currentIndex.value = (currentIndex.value + 1) % images.value.length
    }
    const prevSlide = () => {
      currentIndex.value = (currentIndex.value - 1 + images.value.length) % images.value.length
    }
    const showPreview = (direction) => {
      stopAutoplay()
      const len = images.value.length
      if (len === 0) return
      if (direction === 'left') {
        preview.value.left = images.value[(currentIndex.value - 1 + len) % len]
      } else {
        preview.value.right = images.value[(currentIndex.value + 1) % len]
      }
    }
    const hidePreview = () => {
      startAutoplay()
      preview.value = { left: null, right: null }
    }
    return {
      images,
      currentIndex,
      displayedImages,
      getImageUrl,
      nextSlide,
      prevSlide,
      showPreview,
      hidePreview,
      preview
    }
  },
