    },
    swapStations_2 () {
      if (this.departure_2 && this.arrival_2) {
        [this.departure_2, this.arrival_2] = [this.arrival_2, this.departure_2];
      }
    },
    refreshCaptcha () {
      this.captchaUrl = "/api/placeholder/120/50?" + Date.now();
    },
