    },
    handleResize() {
      if (this.currentPage >= this.menuPages_1.length) {
        this.currentPage = this.menuPages_1.length - 1
      }
    }
  },
  computed: {
    itemPerPage () {
      if (window.innerWidth <= 480) {
        return 5
      } else {
        return 10
      }
    },
    menuPages_1 () {
      const pages = []
      const itemCount = this.menuItems.length
      for (let i = 0; i < itemCount; i += this.itemPerPage) {
        pages.push(this.menuItems.slice(i, i + this.itemPerPage))
      }
      return pages
    },
    menuPages_2 () {
      return [this.articles[this.currPage]]
    }
  },
  mounted() {
    window.addEventListener('resize', this.handleResize)
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.handleResize)
  }
}
